import aboutUs from './components/aboutUs.vue'
import contactUs from './components/contactUs'
import homeFresh from './components/homeFresh'
import myCart from './components/myCart'
import shopFresh from './components/shopFresh'
import shopDetail from './components/shopDetail'


const routes = [{
        path: '/about',
        component: aboutUs,
        name: 'aboutUs'
    },
    {
        path: '/contact',
        component: contactUs,
        name: 'contactUs'
    },
    {
        path: '/',
        component: homeFresh,
        name: 'homeFresh'
    },
    {
        path: '/cart',
        component: myCart,
        name: 'myCart'
    },
    {
        path: '/shop',
        component: shopFresh,
        name: 'shopFresh'
    },
    {
        path: '/detail',
        component: shopDetail,
        name: 'shopDetail'
    },
]

export default routes