<template>
  <div>
    <div class="container my-4">
      <div class="row text-center">
        <h1 class="cart-title">Cart</h1>
      </div>
    </div>
    <div class="container mt-4" v-if="carts.length > 0">
      <table class="table table-warning">
        <thead>
          <tr>
            <th scope="col">Image</th>
            <th scope="col">Product Name</th>
            <th scope="col">Price</th>
            <th scope="col">Quantity</th>
            <th scope="col">Total</th>
            <th scope="col">Remove</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="cart in carts" :key="cart.id">
            <td width="100px">
              <img class="img-fluid rounded" :src="cart.imgUrl" alt="" />
            </td>
            <td>
              <p class="mt-3">{{ cart.name }}</p>
            </td>
            <td>
              <p class="mt-3">{{ cart.price | currencyFormat }}</p>
            </td>
            <td>
              <input
                v-model="cart.quantity"
                class="mt-3 text-center"
                value="0"
                min="0"
                max="20"
                type="number"
              />
            </td>
            <td width="225px">
              <p class="mt-3">{{ (cart.quantity * cart.price) | currencyFormat }}</p>
            </td>
            <td>
              <p class="mt-3">
                <i @click="removeProduct(key)" class="fa-solid fa-xmark"></i>
              </p>
            </td>
          </tr>
          <tr>
            <th>TOTAL:</th>
            <th colspan="5">{{ totalPrice | currencyFormat }}</th>
          </tr>
        </tbody>
      </table>
      <div class="container mt-4">
        <button class="btn btn-primary">Check Out</button>
      </div>
    </div>
    <div v-else class="container">
      <h1>
        No products, please come back
        <router-link :to="{ name: 'homeFresh' }">here</router-link>
      </h1>
    </div>
  </div>
</template>
<script>
import { mapMutations, mapState } from "vuex";
export default {
  computed: {
    ...mapState(["carts"]),
    totalPrice() {
      let price = 0;
      this.carts.forEach(function (cart) {
        price += cart["price"] * cart["quantity"];
      });
      return price;
    },
  },
  methods: {
    ...mapMutations(["removeProduct"]),
  },
  filters: {
    currencyFormat(num) {
      return num.toFixed(2).replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,") + "đ";
    },
  },
};
</script>
<style coped>
.cart-title {
  font-size: 70px;
  margin: 50px 0;
}
.fa-solid {
  font-size: 27px;
  cursor: pointer;
}
</style>
