<template>
  <div>
    <div class="container mt-4">
      <h1>My Shop</h1>
    </div>
    <div class="container">
      <div class="row">
        <div
          class="item col-12 col-md-6 col-xl-3 mt-4 border p-4"
          v-for="item in items"
          :key="item.id"
        >
          <div class="thumb">
            <img :src="item.imgUrl" class="img-fluid" alt="" />
          </div>
          <h1 class="desc my-4">{{ item.name }}</h1>
          <div class="control">
            <button
              type="button"
              @click="handleAddCart(item)"
              class="btn btn-primary me-3"
            >
              Add to Cart
            </button>
            <button
              type="button"
              variant="success"
              @click="gotoDetail(item)"
              class="btn btn-success"
            >
              Detail
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapActions, mapState } from "vuex";
export default {
  computed: {
    ...mapState(["items"]),
  },
  methods: {
    ...mapActions(["handleAddCart"]),
    gotoDetail(item) {
      this.$router.push({ name: "shopDetail", params: item });
      window.scrollTo(0, 0);
    },
  },
};
</script>
<style scoped>
.thumb {
  width: 100%;
  height: 200px;
  overflow: hidden;
}
.item {
  margin-top: 50px;
}
</style>
