<template>
  <div>
    <div class="shopDetail container text-center">
      <h1>Shop Detail</h1>
    </div>
    <div class="container">
      <div
        v-if="Object.keys(details).length !== 0"
        class="productDetail row justify-content-center"
      >
        <div class="col-12 col-md-6">
          <img class="img-thumbnail rounded" :src="details.imgUrl" alt="" />
        </div>
        <div class="detail-text col-12 col-md-6 text-start mt-4">
          <h5 class="text-start mb-3">Name: {{ details.name }}</h5>
          <h5 class="text-start mb-3">Price: {{ details.price | currencyFormat}}</h5>
          <h5 class="text-start mb-3">Description: {{ details.description }}</h5>
          <button
            type="button"
            @click="handleAddCart(details)"
            class="btn btn-primary mt-4 me-4"
          >
            Add to Cart
          </button>
          <button @click="gotoCart" type="button" class="btn btn-success mt-4 me-4">Go To Cart</button>
          <button @click="backHome" type="button" class="btn btn-danger mt-4">
            Buy New
          </button>
        </div>
      </div>
      <div v-else>
        <h1>
          No products have been selected, please come back
          <router-link :to="{ name: 'homeFresh' }">here</router-link>
        </h1>
      </div>
    </div>
  </div>
</template>
<script>
import { mapActions, mapState } from "vuex";
export default {
  data() {
    return {
      details: this.$route.params,
    };
  },
    filters: {
    currencyFormat(num) {
      return  num.toFixed(2).replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,") + "đ";
    },
  },
  computed: {
    ...mapState(["carts"]),
  },
  methods: {
    ...mapActions(["handleAddCart"]),
    gotoCart() {
     
      this.$router.push({ name: "myCart" });
    },
    backHome() {
      this.$router.push({ name: "shopFresh" });
    },
  },
};
</script>
<style scoped>
.shopDetail {
  margin-top: 200px;
  margin-bottom: 150px;
}
.shopDetail h1 {
  font-size: 100px;
  text-transform: uppercase;
  font-weight: 450;
}
.productDetail input {
  width: 50%;
}
.detail-text {
  padding-top: 80px;
}
@media (max-width: 577px) {
}
@media (max-width: 769px) {
}
@media (max-width: 993px) {
  .detail-text {
    padding-top: 0;
  }
  .nav {
    background-color: rgba(var(--bs-light-rgb), var(--bs-bg-opacity));
    left: 0;
    position: absolute;
    top: 100%;
    display: block;
    width: 100%;
  }
  .nav li {
    height: 46px;
    width: 100%;
    text-align: center;
  }
  .nav li a {
    display: block;
    line-height: 46px;
    border-bottom: 1px solid #cecfb3;
    padding: 0 20px;
  }
}
@media (min-width: 1200px) {
}
</style>
