export default {
    increment(state) {
        state.count++
    },
    nextSlider(state) {
        state.indexSlider++;

    },
    prevSlider(state) {
        state.indexSlider--;
    },
    addCart(state, newproduct) {
        state.carts.push(newproduct);
    },

    removeProduct(state, index) {
        state.carts.splice(index, 1);
    },



}