<script>
import { mapActions, mapMutations, mapState } from "vuex";
export default {
  data() {
    return {
      showToast: false,
      positionX: 0,
      indexSlider: 0,
      srcSlider: ["", "", ""],
    };
  },
  components: {},

  methods: {
    nextSlider() {
      this.indexSlider++;
    },
    prevSlider() {
      this.indexSlider--;
    },
    ...mapMutations(["addCart"]),
    ...mapActions(["handleAddCart"]),
   
    addProduct(product) {
      this.handleAddCart(product);
    },
     gotoDetail(item) {
      this.$router.push({ name: "shopDetail", params: item });
      window.scrollTo(0, 0);
    },
  },
  watch: {
    indexSlider() {
      if (this.indexSlider > this.srcSlider.length - 1) {
        this.indexSlider = 0;
      }
      if (this.indexSlider < 0) {
        this.indexSlider = this.srcSlider.length - 1;
      }
      this.positionX = -100 * this.indexSlider;
    },
  },
  computed: {
    changeSilder() {
      return {
        left: this.positionX + "%",
      };
    },
    ...mapState(["products"]),
  },
  mounted() {
    setInterval(() => {
      this.nextSlider();
    }, 7000);
  },
};
</script>
